.shopcar {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  z-index: 50;
}
.shopcar .content {
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
  color: rgba(255,255,255,0.4);
  font-size: 0;
}
.shopcar .content .content-left {
  -ms-flex: 1;
      flex: 1;
}
.shopcar .content .content-left .logo-wrapper {
  display: inline-block;
  position: relative;
