.shopcar .content .content-right {
  -ms-flex: 0 0 105px;
      flex: 0 0 105px;
  width: 105px;
  font-size: 12px;
  font-weight: 700;
  color: rgba(255,255,255,0.4);
  text-align: center;
  line-height: 52px;
  background: #2b333b;
}
.shopcar .content .content-right.not-enough {
